body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.ImageItem-container {
    display: table-row;
    width: 100%;
    clear: both;
    height: 47px;
    margin: 0;
    padding-top: 3px;
    cursor: pointer;
}

.ImageItem-photo {
    background: inherit;
    display: table-cell;
    height: 44px;
    overflow: hidden;
    margin: 0;
    padding-top: 4px;
    padding-right: 11px;
}

.ImageItem-photo-disabled {
    opacity: 0.3;
}

.ImageItem-photo .cover-image {
    width: 44px;
    height: 44px;
    border-radius: 2.5px;
    object-fit: cover;
}

.ImageItem-details {
    display: table-cell;
    width: 100%;
    vertical-align: top;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 9pt;
    padding-top: 6px;
    border-width: 0.5px;
    border-top-style: solid;
    border-color: #ccc;
}

.ImageItem-container:first-child .ImageItem-details {
    border-top: none;
}

.ImageItem-name {
    margin-top: 3px;
}

.ImageItem-description {
    margin-top: 5px;
    color: #888;
}

@media screen and (min-width: 900px) {
    .ImageItem-container {
        float: left;
        display: block;
        width: 208px;
        clear: none;
        height: auto;
        margin: 6px;
        padding: 8px;
        box-shadow: 0 1px 2px rgba(0,0,0,0.07);
        border-color: rgb(68, 68, 68);
        border-bottom-style: none;
        border-width: 0;
        border-image-outset: 0;
        border-image-repeat: stretch stretch;
        border-image-slice: 100%;
        border-image-source: none;
        border-image-width: 1;
        border-left-style: none;
        border-right-style: none;
        border-top-style: none;
    }

    .ImageItem-photo {
        background: #eeeeee;
        display: block;
        width: 208px;
        height: 150px;
        overflow: hidden;
        margin: 0;
        padding: 0;
    }

    .ImageItem-photo .cover-image {
        width: 208px;
        height: 150px;
        border-radius: 2.5px;
        margin: 0;
        object-fit: cover;
    }

    .ImageItem-details {
        display: flex;
        flex-direction: row;
        width: 208px;
        vertical-align: top;
        height: 44px;
        font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
        font-size: 9pt;
        padding-top: 6px;
        border-width: 0;
        margin: 0;
    }

    .ImageItem-container:first-child .ImageItem-details {
        border-top: none;
    }

    .ImageItem-name {
        margin-top: 3px;
        white-space: nowrap;
        overflow-x: hidden;
        text-overflow: ellipsis;
        max-width: 200px;
    }

    .ImageItem-description {
        margin-top: 5px;
        color: #888;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        width: 200px;
        font-size: 12px;
    }

    .ImageItem-info {
        width: 200px;
    }
}




.Article-content {
    margin-top: 50px;
    margin-left: 50px;
    margin-right: 50px;
}

@media screen and (min-width: 900px) {
    .Article-content {
        margin-left: auto;
        margin-right: auto;
        max-width: 800px;
    }
}

